<template>
  <form action="">
    <input type="text" v-model="busca" name="busca" id="busca" placeholder="Buscar...">
    <input type="submit" @click.prevent="buscarProdutos" id="lupa" value="Buscar">
  </form>
</template>

<script>
export default {
    name: 'ProdutosBuscar',
    data() {
      return {
        busca: '',
      }
    },
    methods: {
      buscarProdutos() {
        this.$router.push({ query: {q: this.busca, _page: 1}})
      }
    }
}
</script>

